
* {
  font-size: 100%;
  font: inherit;
}
@font-face {
  font-family: "FontAwesome";
  src: url(/static/fonts/fontawesome-webfont.674f50d.eot);
  src: url(/static/fonts/fontawesome-webfont.674f50d.eot?#iefix) format("embedded-opentype"), url(/static/fonts/fontawesome-webfont.af7ae50.woff2) format("woff2"),
    url(/static/fonts/fontawesome-webfont.fee66e7.woff) format("woff"), url(/static/fonts/fontawesome-webfont.b06871f.ttf) format("truetype"),
    url(/static/img/fontawesome-webfont.912ec66.svg#fontawesomeregular) format("svg");
  font-weight: normal;
  font-style: normal;
}
body {
  background: #1c242d;
}
.text {
  color: white;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  background: #1c242d;
}
section header {
  color: white;
}
section header select {
  color: black;
}
section footer div {
  margin: 0 auto;
  width: 400px;
}
.se-pre-con {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 999;
  background: url(/static/img/loader.6d90f29.gif) center no-repeat #1c242d;
}
.center_text {
  position: absolute;
  min-width: 400px;
  height: 50px;
  top: 50%;
  left: 50%;
  margin-left: -200px;
  margin-top: 30px;
}
.loading_message {
  margin-top: 60px;
}
.clickable {
  cursor: pointer;
}
.sorted {
  white-space: nowrap;
}
.avatar {
  padding-right: 5px;
}
.player_link:hover {
  text-decoration: none;
}
table {
  font-size: 10pt;
  color: #d9d9d9;
  position: relative;
  margin: 0 auto;
  border: 2;
  border-color: #3b3a38;
}
thead {
  font-weight: bold;
}
thead tr {
  background-color: #000000;
}
thead td {
  min-width: 80px;
  width: 50px;
}
td {
  min-height: 50px;
  padding: 10px 10px 10px 10px;
}
td.center {
  vertical-align: center;
}
td.info {
  text-align: left;
  min-width: 200px;
  white-space: nowrap;
}
table.sortable span.arrow {
  display: inline-block;
  border-style: solid;
  border-width: 6px;
  font-size: 0;
  border-color: transparent transparent white transparent;
  line-height: 0;
  height: 0;
  width: 0;
  margin-top: -7px;
  margin-left: 6px;
  vertical-align: middle;
}
table.sortable span.arrow.desc {
  margin-top: 1px;
  border-color: white transparent transparent transparent;
}
table.sortable th.nosort {
  color: #c4c4c4;
}
img.flag {
  float: right;
  margin-top: 10px;
}
tr {
  background-color: #202020;
}
tr.alternative {
  background-color: #181818;
}
ul {
  padding: 0;
  list-style-type: none;
}
li {
  display: inline;
  margin: 5px 5px;
}
.paging a.current {
  color: red;
}
.paging a {
  color: #999;
}
.paging a.first::after {
  content: "...";
}
.paging a.last::before {
  content: "...";
}
.float_left {
  float: left;
}
.winrate {
  background-color: red;
  width: 200px;
  height: 4px;
}
.winrate_text {
  color: #a9cf54;
}
.winrate div {
  background: #a9cf54;
  height: 4px;
}

